
.card-container {
    width: 90%;
    overflow: hidden;
    box-shadow:  0px 0px 15px -5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 60px;
    transition: 0.3s;
    animation: ease-in;
    font-size: 12px;
    background-color: white;

}

/* .li1, .li2, .li3, .li4, .li5, .li6

} */

.card-content {
    width: 250px;
    text-align: justify;
}

.image-container img {
    width: 650px;
    height: 380px;
    padding-top: 10px;
}

.content {
    display: flex;
    justify-content: start;
    align-items: flex-start;
}

.page-title{
    margin-bottom: 10px;
}

ul li {
    text-align: start;
}

.quote-line {
    text-align: center;
}
